* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-img {
  width: 20px;
  height: 12px;
  margin-right: 8px;
}
.country-img_info {
  width: 30px;
  height: 18px;
}
.country-name {
  font-size: 24px;
}
.country {
  display: flex;
  align-items: center;
}
.country-list {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.country-lists {
  display: flex;
  align-items: center;
  flex-direction: row;
}
